import React, { useState } from 'react';
import styled from 'styled-components';
import { getPhrases, getSentences, getParagraphs } from './utils';
import map from './loader';

const STORY = `31

2118221182112

I am Wang Chi LinI am from Taiwan



1100731`;

const TONE_DOT = '';
const TONE_2ND = '';
const TONE_3RD = '';
const TONE_4TH = '';

const OriginalStory = styled.textarea`
  height: 150px;
  width: calc(100% - 2px - 10px);
  margin: 0 0 5px 0;
  padding: 5px;
`;

const ControlPanel = styled.div`
  height: 30px;
  width: calc(100% - 2px - 10px);
  margin: 5px 0;
  // border: 1px solid #000;
  padding: 5px;
`;

const ZuYinStory = styled.div`
  font-family: DFKai-sb, ;
  writing-mode: vertical-rl;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
  align-content: flex-start;
  height: calc(100% - 10px - 150px - 30px - 10px - 10px - 2px * 4 - 5px * 6);
  width: calc(100% - 2px - 10px);
  border: 1px solid #000;
  text-align: left;
  margin: 5px 0 0 0;
  padding: 5px;
  overflow-x: scroll;
  overflow-y: auto;
`;

const Paragraph = styled.div`
  writing-mode: vertical-rl;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
  align-content: flex-start;
  height: 100%;
  // border: 1px solid red;
  text-align: left;
  margin: 5px 0 0 0;
  padding: 0;
  overflow-x: scroll;
  overflow-y: auto;
`;

const CharacterZuYinBlock = styled.div`
  display: flex;
  flex-direction: column-reverse;
  width: 75px;
  height: 60px;
  // border: 1px solid black;
`;

const CharacterBlock = styled.div`
  writing-mode: rl;
  width: 50px;
  height: 50px;
  // border: 1px solid blue;
  text-align: left;
  line-height: 50px;
  font-size: 50px;
  margin: 0;
  padding: 0;
`;
const ZuYinBlock = styled.div`
  writing-mode: rl;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  margin: 0;
  padding: 0;
  width: 13px;
  height: 50px;
  // line-height: 13px;
  font-size: 13px;
  // border: 1px solid red;
`;
const Phonetic = styled.div`
  writing-mode: rl;
  margin: 0;
  padding: 0;
  width: 13px;
  height: 13px;
  text-align: center;
  // border: 1px solid red;
  line-height: 13px;
  font-size: 13px;
`;
const Tone = styled.div`
  writing-mode: rl;
  margin: ${(prop) => {
    switch (prop.type) {
      case TONE_DOT:
        return '0 0 -5px 0';
      case TONE_2ND:
        return '-15px -8px 0 0';
      case TONE_3RD:
        return '-15px -8px 0 0';
      case TONE_4TH:
        return '-15px -8px 0 0';
      default:
        return '0';
    }
  }};
  padding: 0;
  width: 13px;
  height: 13px;
  text-align: center;
  // border: 1px solid green;
  line-height: 13px;
  font-size: 13px;
`;

const WordBlock = (char, zuYin, idx) => {
  let zuYinBlock = null;
  if (zuYin) {
    if (zuYin.includes(TONE_DOT)) {
      zuYinBlock = (
        <ZuYinBlock>
          <Tone type={TONE_DOT}>{TONE_DOT}</Tone>
          {zuYin
            .split('')
            .filter((z) => z !== TONE_DOT)
            .map((z) => (
              <Phonetic key={z}>{z}</Phonetic>
            ))}
        </ZuYinBlock>
      );
    } else if (zuYin.includes(TONE_2ND)) {
      zuYinBlock = (
        <ZuYinBlock>
          {zuYin
            .split('')
            .filter((z) => z !== TONE_2ND)
            .map((z) => (
              <Phonetic key={z}>{z}</Phonetic>
            ))}
          <Tone type={TONE_2ND}>{TONE_2ND}</Tone>
        </ZuYinBlock>
      );
    } else if (zuYin.includes(TONE_3RD)) {
      zuYinBlock = (
        <ZuYinBlock>
          {zuYin
            .split('')
            .filter((z) => z !== TONE_3RD)
            .map((z) => (
              <Phonetic key={z}>{z}</Phonetic>
            ))}
          <Tone type={TONE_3RD}>{TONE_3RD}</Tone>
        </ZuYinBlock>
      );
    } else if (zuYin.includes(TONE_4TH)) {
      zuYinBlock = (
        <ZuYinBlock>
          {zuYin
            .split('')
            .filter((z) => z !== TONE_4TH)
            .map((z) => (
              <Phonetic key={z}>{z}</Phonetic>
            ))}
          <Tone type={TONE_4TH}>{TONE_4TH}</Tone>
        </ZuYinBlock>
      );
    } else {
      zuYinBlock = (
        <ZuYinBlock>
          {zuYin.split('').map((z) => (
            <Phonetic key={z}>{z}</Phonetic>
          ))}
        </ZuYinBlock>
      );
    }
  }

  return (
    <CharacterZuYinBlock key={idx}>
      <CharacterBlock>{char}</CharacterBlock>
      {zuYinBlock}
    </CharacterZuYinBlock>
  );
};

const WordBlocks = (phrases, idx) => {
  const chars = phrases.map((p) => p.split('')).flat();
  const zuYins = phrases.map((p) => (map[p] ? map[p].split(' ') : null)).flat();

  return chars.map((c, i) => WordBlock(c, zuYins[i]?.replace('-', ''), `${idx}_${i}`));
};

const App = () => {
  const [storyTexts, setStoryTexts] = useState(STORY);
  const paragraphs = getParagraphs(storyTexts);
  const paragraphComp = paragraphs.map((paragraph, idx) => {
    const sentences = getSentences(` ${paragraph}`, map);
    const phrasesInSentences = sentences.map((sentence) => getPhrases(sentence, map));
    const words = phrasesInSentences.map((phrase, i) => WordBlocks(phrase, i));
    return {
      words: words.flat(),
      key: idx,
    };
  });

  return (
    <>
      <OriginalStory
        value={storyTexts}
        onChange={(evt) => {
          setStoryTexts(evt.target.value);
        }}
      />
      <ControlPanel>
        <a href="https://github.com/AndyWu/BunnyLionCrocodile">BunnyLionCrocodile</a>
        {' '}
        App: 
      </ControlPanel>
      <ZuYinStory>
        {paragraphComp.map((p) => <Paragraph key={p.key}>{p.words}</Paragraph>)}
      </ZuYinStory>
    </>
  );
};

export default App;
